<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <!-- <HelloWorld msg="Our first app in VUE CLI"/> -->

  <div>
  <template v-if="this.question">
  
    <h1 v-html="this.question"></h1>
    <template v-for="(reponse, index) in this.answers" v-bind:key="index">
      <div class="flex">
      <input type="radio"
      :disabled="this.submited"
       name="options" :id="index"
       v-model="this.chosen"  
       :value="reponse">  
      <label :for="index" v-html="reponse"></label>
      </div><br>

    </template>   
          <button v-if="!this.submited" class="send" @click="this.submit()" type="button">Send</button>  
          <section class="result" v-if="submited">
          <h4 v-if="this.chosen != this.correct" 
          v-html="'&#10060; I\'m sorry, you picked the wron answer. The correct answer is &quot' +this.correct+'&quot.'"></h4> 
          <h4 v-else 
          v-html="'&#9989; Congratulation! The  answer  &quot' + this.correct + '&quot is correct'"></h4> 
          <br>
          <button class="send" @click="this.getQuestion()" type="button">Next question</button> 
          </section>
     
  </template>   
     
     
    <!-- <input type="radio" name="options" id="" value="False">
    <label for="">False</label><br> -->

  
  </div> 
</template> 
   
<script>
      // import HelloWorld from './components/HelloWorld.vue'
      

      export default {
        name: 'App',
        components: {
          // HelloWorld 
        },  
        data() {
          return {
            question : undefined, 
            incorrects : undefined,
            correct : undefined,
            chosen : undefined,
            submited : false,
          }
        },
        methods :{
          submit(){
            if (!this.chosen) {
              alert('No answer chosen ')
            } else {
              this.submited = true
              if (this.chosen == this.correct) {
                console.log('All rigth')
              }else{
                console.log('wrond !')
              }
            }
          },
          getQuestion(){

            this.submited = false 
            this.chosen = null 
            this.axios
            .get("https://opentdb.com/api.php?amount=1&category=18")
            .then((response) => {
              this.question = response.data.results[0].question
              this.correct= response.data.results[0].correct_answer
              this.incorrects = response.data.results[0].incorrect_answers
              });

          }
        }
        ,

        computed:{
          answers(){
            var answers = JSON.parse(JSON.stringify(this.incorrects))  ;
            answers.splice(Math.round(Math.random()*answers.length),0,this.correct)

            return answers
          } 
        },

        
      
        created(){
          this.getQuestion()
        },
        
      }   

</script>

